.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  padding: 1rem;
}

.modal-overlay.show {
  opacity: 1;
  visibility: visible;
}

.modal-content {
  background: white;
  border-radius: 12px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  width: 100%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  animation: slideIn 0.3s ease-out;
  font-family: 'Poppins', sans-serif;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1.5rem 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  background: #D4AF5F;;
  color: #5F646D;
}

.modal-header h2 {
  color: #1B1D21;
  font-size: 1.5rem;
  font-weight: 600;
  margin: 0;
  order: 1;
}

.close-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 6px;
  color: #1B1D21;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  order: 2;
}

.close-btn:hover {
  background-color: rgba(27, 29, 33, 0.1);
  color: #1B1D21;
}

.close-btn svg {
  width: 20px;
  height: 20px;
}

/* Form Styles */
.change-manager-form {
  display: flex;
  flex-direction: column;
}

.form-content {
  padding: 1.5rem;
}

/* Department Info */
.department-info {
  text-align: center;
  margin-bottom: 2rem;
  padding: 1.5rem;
  background-color: #f9fafb;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
}

.department-info h3 {
  color: #374151;
  font-size: 1rem;
  font-weight: 600;
  margin: 0 0 0.5rem 0;
}

.department-info p {
  color: #111827;
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0 0 0.5rem 0;
}

.department-code {
  display: inline-block;
  background-color: #e0e7ff;
  color: #3730a3;
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.875rem;
  font-weight: 600;
  font-family: 'Courier New', monospace;
}

/* Error Message */
.error-message {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem;
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 8px;
  color: #991b1b;
  margin-bottom: 1.5rem;
  font-size: 0.875rem;
}

.error-message .error-icon {
  color: #ef4444;
  flex-shrink: 0;
}

/* Search Container */
.search-container {
  margin-bottom: 1.5rem;
}

.search-container label {
  display: block;
  margin-bottom: 0.5rem;
  color: #374151;
  font-weight: 500;
  font-size: 0.875rem;
}

.search-input-wrapper {
  position: relative;
}

.search-icon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: #9ca3af;
  width: 20px;
  height: 20px;
}

.search-input-wrapper input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 3rem;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 0.875rem;
  color: #374151;
  background: white;
  transition: all 0.2s ease;
  font-family: 'IBM Plex Sans Arabic', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

.search-input-wrapper input:focus {
  outline: none;
  border-color: #D4AA3A;
  box-shadow: 0 0 0 3px rgba(212, 170, 58, 0.1);
}

/* Users List */
.users-list {
  margin-bottom: 1.5rem;
}

.users-list label {
  display: block;
  margin-bottom: 0.75rem;
  color: #374151;
  font-weight: 500;
  font-size: 0.875rem;
}

.users-container {
  max-height: 300px;
  overflow-y: auto;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  background: white;
}

.user-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
  border-bottom: 1px solid #f3f4f6;
}

.user-item:last-child {
  border-bottom: none;
}

.user-item:hover {
  background-color: #f9fafb;
}

.user-item.selected {
  background-color: #fdf8f0;
  border-color: #D4AA3A;
}

.user-avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: #e5e7eb;
  border-radius: 50%;
  color: #6b7280;
  flex-shrink: 0;
}

.user-info {
  flex: 1;
  min-width: 0;
}

.user-name {
  color: #111827;
  font-weight: 500;
  font-size: 0.875rem;
  margin-bottom: 0.25rem;
}

.user-email {
  color: #6b7280;
  font-size: 0.75rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.user-radio {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.radio-circle {
  width: 20px;
  height: 20px;
  border: 2px solid #d1d5db;
  border-radius: 50%;
  position: relative;
  transition: all 0.2s ease;
}

.radio-circle.selected {
  border-color: #D4AA3A;
  background-color: #D4AA3A;
}

.radio-circle.selected::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 8px;
  height: 8px;
  background-color: white;
  border-radius: 50%;
}

/* No Users */
.no-users {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  text-align: center;
  color: #6b7280;
}

.no-users-icon {
  color: #9ca3af;
  margin-bottom: 1rem;
}

.no-users p {
  margin: 0;
  font-size: 0.875rem;
}

/* Modal Footer */
.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1rem 1.5rem 1.5rem;
  border-top: 1px solid #e5e7eb;
  background-color: #f9fafb;
  border-radius: 0 0 12px 12px;
}

/* Button Styles */
.btn {
  font-family: 'IBM Plex Sans Arabic', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  padding: 0.75rem 1.5rem;
  font-size: 0.875rem;
  min-width: 100px;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-primary {
  background: linear-gradient(135deg, #D4AA3A 0%, #B68A35 100%);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background: linear-gradient(135deg, #B68A35 0%, #92722A 100%);
  box-shadow: 0 4px 8px rgba(212, 170, 58, 0.3);
}

.btn-secondary {
  background: transparent;
  color: #1B1D21 !important;
  border: 1px solid #1B1D21 !important;
}

.btn-secondary:hover:not(:disabled) {
  background: #1B1D21;
  color: #FFFFFF !important;
}

.btn-content {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.loading-spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* RTL Support */
.modal-content.rtl .modal-header {
  flex-direction: row-reverse;
}

.modal-content.rtl .modal-header h2 {
  order: 2;
}

.modal-content.rtl .close-btn {
  order: 1;
}

.modal-content.rtl .modal-footer {
  flex-direction: row-reverse;
}

.modal-content.rtl .search-icon {
  left: auto;
  right: 1rem;
}

.modal-content.rtl .search-input-wrapper input {
  padding: 0.75rem 3rem 0.75rem 1rem;
}

.modal-content.rtl .user-item {
  flex-direction: row-reverse;
}

/* Responsive Design */
@media (max-width: 768px) {
  .modal-content {
    max-width: 95vw;
    margin: 0.5rem;
  }
  
  .modal-footer {
    flex-direction: column;
  }
  
  .btn {
    width: 100%;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .modal-content {
    max-width: 100vw;
    margin: 0;
    border-radius: 0;
    max-height: 100vh;
  }
  
  .modal-header,
  .form-content,
  .modal-footer {
    padding: 1rem;
  }
  
  .modal-header h2 {
    font-size: 1.25rem;
  }
  
  .department-info {
    padding: 1rem;
  }
  
  .user-item {
    padding: 0.75rem;
  }
  
  .user-avatar {
    width: 32px;
    height: 32px;
  }
}
